<template>
  <div>
    <p class="mb-20">:link, :visited, :hover, :focus, :active, :checked 使用js修改元素的样式属性或追加删除样式来执行定义的动画</p>
    <p class="mb-20">Transition ＋ Transform ＝ 两个关键帧的Animation</p>
    <p class="mb-20">transition允许css的属性值在一定的时间区间内平滑地过渡。圆滑地以动画效果改变CSS的属性值</p>

    <table class="table mb-20">
      <thead>
        <tr>
          <th>属性</th>
          <th>4个子属性</th>
        </tr>
      </thead>
      <tbody v-for="item in transition">
        <tr v-for="(el, index) in item.list">
          <td :rowspan="item.list.length" v-if="index == 0" style="width:200px;">{{item.name}}</td>
          <td>{{el}}</td>
        </tr>
      </tbody>
    </table>

    <table class="table mb-20">
      <thead>
        <tr>
          <th>属性</th>
          <th>4个子属性</th>
        </tr>
      </thead>
      <tbody v-for="item in transitionValue">
        <tr v-for="(el, index) in item.list">
          <td :rowspan="item.list.length" v-if="index == 0" style="width:200px;">{{item.name}}</td>
          <td>{{el}}</td>
        </tr>
      </tbody>
    </table>

    <table class="table mb-20">
      <thead>
        <tr>
          <th>支持样式</th>
          <th>4个子属性</th>
        </tr>
      </thead>
      <tbody v-for="item in supportStyles">
        <tr v-for="(el, index) in item.list">
          <td :rowspan="item.list.length" v-if="index == 0" style="width:200px;">{{item.name}}</td>
          <td>{{el}}</td>
        </tr>
      </tbody>
    </table>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        transition: [{
        	name: 'transition',
        	list: [
        		'transition-property, 过渡属性',
        		'transition-duration, 在延续时间段',
        		'transition-timing-function, 变换的速率变化',
        		'transition-delay',
        		'property duration function delay, property duration function delay;'
        	]
        }],

        transitionValue: [
        	{
        		name: 'transition-property',
        		list: [
        			'all 元素任何可过渡（transition）属性值变化时都将执行过渡效果。',
        			'none 本身有过渡样式,指定为none时，动画立即停止。',
        			'background-color, height, width, margin-left;'
        		]
        	},
        	{
        		name: 'transition-duration',
        		list: [
        			'1s, 200ms; 过渡持续时间。'
        		]
        	},
        	{
        		name: 'transition-timing-function',
        		list: [
        			'ease：（逐渐变慢）默认值，等同于贝塞尔曲线(0.25, 0.1, 0.25, 1.0)',
        			'linear：等同于贝塞尔曲线(0.0, 0.0, 1.0, 1.0)',
        			'ease-in：等同于贝塞尔曲线(0.42, 0, 1.0, 1.0)',
        			'ease-out：等同于贝塞尔曲线(0, 0, 0.58, 1.0)',
        			'ease-in-out：等同于贝塞尔曲线(0.42, 0, 0.58, 1.0)',
        			'cubic-bezier(.17, .86, .73, .14) 通过贝塞尔曲线来计算“转换”过程中的属性值'
        		]
        	},
        	{
        		name: 'transition-delay',
        		list: [
        			'1s, 200ms;'
        		]
        	},
        ],

        supportStyles: [
        	{
        		name: '颜色',
        		list: [
        			'background-color, background-position, background-image, color'
        		]
        	},
        	{
        		name: '边框',
        		list: [
        			'border-bottom-color, border-bottom-width, border-color, border-width, border-spacing'
        		]
        	},
        	{
        		name: '间距',
        		list: [
        			'padding, top, width, max-width, min-width'
        		]
        	},
        	{
        		name: '透明度',
        		list: [
        			'opacity, outline-color, outline-offset, outline-width, visibility, z-index, zoom'
        		]
        	},
        	{
        		name: '文本',
        		list: [
        			'font-size, font-weight, letter-spacing, line-height, text-indent, text-shadow, vertical-align, word-spacing'
        		]
        	}
        ]
      }
    }
  }
</script>
